body {
	margin: 0;
	padding: 0;
	font-family: sans-serif;
}

* {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	-webkit-font-smoothing: antialiased;
}

.ais-SearchBox-submit,
.ais-SearchBox-reset {
	display: none;
}

.Pattern {
	position: absolute;
	top: 50%;
	left: 50%;
	width: 400px;
	height: 400px;
	margin-top: -200px;
	margin-left: -200px;
	overflow: hidden;
	color: rgba(255, 255, 255, 0.1);
	font-family: 'Open Sans', Helvetica, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	-webkit-transform: scale(1.5);
	    -ms-transform: scale(1.5);
	        transform: scale(1.5);
}
.horizantal {
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
}

.horizantal .stripe {
	position: relative;
	float: left;
	width: 40px;
	height: 10px;
	margin: 15px 0;
	background: #444;
	-webkit-animation: rotate-h 20s ease-in-out infinite;
	        animation: rotate-h 20s ease-in-out infinite;
}

.vertical {
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
}

.vertical .stripe {
	position: relative;
	float: left;
	width: 10px;
	height: 40px;
	margin: 0 15px;
	background: #444;
	-webkit-animation: rotate-v 20s ease-in-out infinite;
	        animation: rotate-v 20s ease-in-out infinite;
}

@-webkit-keyframes rotate-h {
	0% {
		-webkit-transform: rotate(0);
		        transform: rotate(0);
	}
	25% {
		-webkit-transform: rotate(45deg);
		        transform: rotate(45deg);
	}
	50% {
		-webkit-transform: rotate(45deg) translateX(-20px);
		        transform: rotate(45deg) translateX(-20px);
	}
	75% {
		-webkit-transform: rotate(0deg) scaleY(2) scaleX(0.5);
		        transform: rotate(0deg) scaleY(2) scaleX(0.5);
	}
	100% {
		-webkit-transform: rotate(90deg);
		        transform: rotate(90deg);
	}
}

@keyframes rotate-h {
	0% {
		-webkit-transform: rotate(0);
		        transform: rotate(0);
	}
	25% {
		-webkit-transform: rotate(45deg);
		        transform: rotate(45deg);
	}
	50% {
		-webkit-transform: rotate(45deg) translateX(-20px);
		        transform: rotate(45deg) translateX(-20px);
	}
	75% {
		-webkit-transform: rotate(0deg) scaleY(2) scaleX(0.5);
		        transform: rotate(0deg) scaleY(2) scaleX(0.5);
	}
	100% {
		-webkit-transform: rotate(90deg);
		        transform: rotate(90deg);
	}
}

@-webkit-keyframes rotate-v {
	0% {
		-webkit-transform: rotate(0);
		        transform: rotate(0);
	}
	25% {
		-webkit-transform: rotate(45deg);
		        transform: rotate(45deg);
	}
	50% {
		-webkit-transform: rotate(45deg) translateX(10px);
		        transform: rotate(45deg) translateX(10px);
	}
	75% {
		-webkit-transform: rotate(45deg) scaleX(2) scaleY(0.5);
		        transform: rotate(45deg) scaleX(2) scaleY(0.5);
	}
	100% {
		-webkit-transform: rotate(90deg);
		        transform: rotate(90deg);
	}
}

@keyframes rotate-v {
	0% {
		-webkit-transform: rotate(0);
		        transform: rotate(0);
	}
	25% {
		-webkit-transform: rotate(45deg);
		        transform: rotate(45deg);
	}
	50% {
		-webkit-transform: rotate(45deg) translateX(10px);
		        transform: rotate(45deg) translateX(10px);
	}
	75% {
		-webkit-transform: rotate(45deg) scaleX(2) scaleY(0.5);
		        transform: rotate(45deg) scaleX(2) scaleY(0.5);
	}
	100% {
		-webkit-transform: rotate(90deg);
		        transform: rotate(90deg);
	}
}



